export default {
  layout: "default",
  data() {
    return {
      data: [],
      next: "",
      subTagList: [],
      selectedData: false,
      subTagData: [],
      fastCheck: [],
      seoObject: {}
    };
  },
  head() {
    return {
      title: this.seoObject.page_title,
      meta: [
        {
          hid: "description",
          name: "description",
          content: this.seoObject.description
        },
        {
          hid: "keywords",
          name: "keywords",
          content: this.seoObject.meta_keyword
        },
        {
          hid: "twitter:card",
          name: "twitter:card",
          content: this.seoObject.page_title
        },
        {
          hid: "twitter:title",
          name: "twitter:title",
          content: this.seoObject.page_title
        },
        {
          hid: "twitter:description",
          name: "twitter:description",
          content: this.seoObject.description
        },
        {
          hid: "og:title",
          property: "og:title",
          content: this.seoObject.page_title
        },
        {
          hid: "og:type",
          property: "og:type",
          content: "article.text"
        },
        {
          hid: "og:url",
          property: "og:url",
          content: ""
        },
        {
          hid: "og:description",
          name: "og:description",
          content: this.seoObject.description
        },
        {
          hid: "og:image",
          name: "og:image",
          content: this.seoObject.meta_image
        }
      ]
    };
  },
  async fetch() {
    var self = this;
    //seo
    var self = this;

    await self.$axios
      .$get(process.env.baseUrl + "/s/h/qandaPage")
      .then(function(posts) {
        self.seoObject = posts;
      })
      .catch(function(error) {
        console.log(error);
      });
    await self.$axios
      .$get(process.env.baseUrl + "/q&a/api/v1/short_list")
      .then(function(posts) {
        self.subTagList = posts.results;
      })
      .catch(function(error) {
        console.log(error);
      });

    await self.$axios
      .$get(process.env.baseUrl + "/q&a/api/v1/")
      .then(function(posts) {
        self.data = posts.results;
        self.next = posts.next;
      })
      .catch(function(error) {
        console.log(error);
      });

    await self.$axios
      .$get(process.env.baseUrl + "/q&a/api/v1/qna_fast_check")
      .then(function(posts) {
        self.fastCheck = posts;
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    async loadData() {
      if (this.next != null) {
        var self = this;
        await self.$axios
          .$get(self.next)
          .then(function(posts) {
            posts.results.forEach(element => {
              if (!self.selectedData) {
                self.data.push(element);
              } else {
                self.subTagData.push(element);
              }
            });
            self.next = posts.next;
          })
          .catch(function(error) {
            console.log("No Net" + error);
          })
          .finally(function() {});
      } else {
        // alert("Null");
      }
    },
    async showSubTagPosts(item) {
      var self = this;
      await self.$axios
        .$get(item.shot_list_data)
        .then(function(posts) {
          // console.log(posts);
          // self.data = "";
          self.subTagData = posts.results;
          self.next = posts.next;
          self.selectedData = true;
          // subtag
        })
        .catch(function(error) {
          console.log("No Net" + error);
        });
    },
    async setView(view, slug) {
      // this.$axios
      //   .$put(process.env.baseUrl + `/q&a/api/v1/dtls/${slug}`, {
      //     view: view + 1
      //   })
      //   .then(function(response) {})
      //   .catch(function(e) {
      //     console.log("setview " + e);
      //   });
    }
  }
};
